.prslide-container{
    width: 100%;
    direction: ltr;
    display: flex;
    justify-content:center;
    align-items: center;
    padding: 20px 5px 50px 5px;
    height: 12vw;
    background-color: blue;
    
}
.prslidepart0{
    width:15vw;
    height: 13vw;
    background-size:15vw 13vw;
    background-repeat: no-repeat;
    transition-property: transform,left,display;
    transition-duration: 1s;
    border-radius: 10px;
    border: solid 2px transparent;
    margin: auto 10px;

}
.prslidepart0:hover{
    border: solid 2px rgb(154, 104, 157);
    cursor: pointer;
    border-radius:20px;
}
