.socalicons{
    position: fixed;
    top:0px;
    left: 0px;
    display: flex;
    align-items:center;
    background-color: blue;
    height: 40px;
    width: 100%;
    z-index: 300;
    margin: 0px;
    padding: 0px;
}
.social{
    width: 25px;
    height: 25px;
    border-radius: 50%;
    margin-left: 5px;
    cursor: pointer;
    z-index: 300;

}
.logo{
    position: fixed;
    left: 50%;
    top:5px;
    transform: translate(-50%, 0);
    z-index: 300;
}